#changelist-form {
	position: relative;
}
#changelist-form.active .actions-nav {
	display: block;
}
#changelist-form.active .action-checkbox-column{
	background-color: $gray-lighter;
}
#changelist-form .action-checkbox-column{
	height: 36px;
	width: 36px;
}

#changelist-form .actions-nav {
	position: absolute;
	right: 0;
	left: 36px;

	display: none;
	background-color: $white;
		
	.btn {
		padding: 7px 12px;
	}
}

.btn-default .badge {
	color: $white;
    background-color: $gray;
}
#result_list .action-checkbox-column, #result_list .action-checkbox {
	text-align: center;
}

#result_list thead a {
	color: $gray;
	&:hover,
	&:focus {
		color: $gray;
	}
}

#changelist-form .table .selected td, #changelist-form .table .selected th {
	background-color: theme-color("warning");

}

#filters .dropdown {
	border-left: 1px $navbar-default-border solid;
}
.sortremove i{
	color: theme-color("danger");
}

.empty-list {
	padding-top: 2%;

	text-align: center;

	.breadcrumbs_icon {
		color: $gray-lighter;
		font-size: 6em;
	}
	.text {
		margin: 1.5em 0;

		color: $gray-light;
	}
}
