$sidebar-width: 350px;
$sidebar-padding: $sidebar-width + 15px;
.sidebar {
	grid-area: sidebar;

    background-color: $gray-dark;
    z-index: 999;
    // padding: 15px;
}
.content {
	grid-area: main;
	padding: 15px;
}
.navbar-nav .well {
	background-color: $gray;
	border-color: $gray;
	color: $white;
}
// .user-nav li .dropdown-toggle {
	// padding-left: 0;
	// margin-left: -15px;
// }
.sidebar .navbar.navbar-inverse {
	height: 100%;
}

.nav.navbar-nav .nav.navbar-nav {
	padding-left: 15px;
}
.main-nav a {
	border-bottom: 1px solid $gray;
}
.menu-parent + ul.navbar-nav {
	border-bottom: 1px solid transparent;
	// transition: border-color 0.2s ease-in-out;
}
.menu-parent[aria-expanded='true'],
.menu-parent[aria-expanded='true'] + ul.navbar-nav{
	border-color: $gray;
}
ul.navbar-nav {
	padding-top: 15px;
	padding-bottom: 15px;
}
@media (min-width: 768px) {
	.sidebar {
		.navbar-collapse {
			height: auto;
			border-top: 0;
			box-shadow: none;
			max-height: none;
			padding-left:0;
			padding-right:0;
		}
		.navbar-collapse.collapse {
			display: block;
			width: auto;
			padding-bottom: 0;
			overflow: visible;
		}
		.navbar-collapse.in {
			overflow-x: visible;
		}

		.navbar {
			max-width:300px;
			margin-right: 0;
			margin-left: 0;
		}	

		.navbar-nav,
		.navbar-nav li,
		.navbar-left,
		.navbar-right,
		.navbar-header {
			float:none;
		}

		.navbar-right .dropdown-menu {
			left: 0;
			right:auto;
		}
		.navbar-collapse .navbar-nav.navbar-right:last-child {
		    margin-right: 0;
		}
	}
}

.navbar-header {
	color: $white;
}

.navbar-header a {

		// color: $navbar-inverse-brand-color;

		&:hover,
		&:focus,
		&:active {
			// color: $navbar-inverse-brand-hover-color;
			text-decoration: none;
			cursor: pointer;
			cursor: hand;
		}
		.fa {
			margin-right: 10px;
		}
}
.navbar-header .dropdown {
	display: inline-block;
} 

.navbar-header .navbar-right {
	margin-right: auto;
}