@import "./node_modules/bootstrap/scss/bootstrap";
@import "font-awesome";

@mixin font-size($sizeValue) {
	font-size: $sizeValue + px;
	font-size: ($sizeValue / 16) + em;
}

html {
	//overwriting bootstrap's infamous 10px rule
	font-size: inherit;
}
body {
	font-size: 100%;
	font-weight: 300;
}

.main-layout {
  display: grid;
  justify-items: stretch;
  grid-template-columns: 250px auto;
  grid-template-rows: 60px auto 28px;
  grid-auto-flow: column;
  grid-template-areas: 
    "sidebar header"
    "sidebar main"
    "sidebar footer";
}



.form-group .errorlist {
	position: absolute;
	top: 100%;
	list-style: none;
	margin: 5px 0px 0px 0px;
	padding: 0px;
	background-color: #FFF;
	color: theme-color("success");
	//font-size: $font-size-small;
	box-shadow: 0 0 8px 0 rgba(0,0,0,0.2);
	li {
		padding: 5px 10px;
		list-style: none;
	}
	&:before {
		content: '';
		position: absolute;
		top: -8px;
		left: 10px;
		width: 0px;
		height: 0px;
		border-bottom: 8px #FFF solid;
		border-left: 8px transparent solid;
		border-right: 8px transparent solid;
	}
}

$gray-dark: #CCC;
$gray-base: #CCC;
$gray-lighter: #CCC;
$gray-light: #CCC;
$gray: #CCC;
$navbar-default-border: #CCC;

.btn-inverse {
	color: $white;
	background-color: $gray-dark;
	border-color: $gray-dark;

	&:hover,
	&:focus,
	&:active {
		color: $white;
		background-color: lighten($gray-dark, 20%);
		border-color: lighten($gray-dark, 20%);
	}
}
a,
.btn {
	&:hover,
	&:focus {	
		// text-decoration: none;
	}
}

.btn a {
	color: inherit;
	text-decoration: inherit;
}

.page-header {
	display: none;
}


.btn-toolbar {
    position: fixed;
    top: 0;
    right: 15px;
    z-index: 999;

    padding: 10px 0;
}

.object-tools li {
	list-style-type: none;
	.btn {
		padding: 5px 0;
		// color: $gray-dark;

		&:hover,
		&:focus {
			// color: $gray-light;
		}
	}
}

.related-links {
	position: absolute;
    top: 0px;
    right: 0px;
}
.form-group {
	position: relative;
}

.inline-group.row {
	margin-top: 30px;
}

fieldset.module.row, .inline-group.row {
	margin-top: 30px;	
	padding-bottom: 25px;
	border-bottom: 1px lighten($gray-base, 90%) solid;
}

.inline-group.row fieldset.module.row {
	border: 0;
	margin-top: 0;
}

fieldset.module.row h3, .inline-group.row h3 {
	margin-top: 0;
}

.form-actions {
    margin-top: 65px;
}
.btn-toolbar .btn-group .btn{
	&:not(:last-child) {
		margin-right: 10px;
	}
}
.form-group .control-label {
	display: block;
}

