/* SELECTOR (FILTER INTERFACE) */
.selector {
    float:none;

    display: block;

    &:after {
        display: table;
        clear: left;
        content: "";
    }
}

.selector select,
.selector-chosen select {
    height: 14.2em;
    // width:100%;
    // margin:0px;
    // border-radius: 0;
    // padding:6px 6px;

    // @include box-sizing(border-box);
    @extend .form-control;
}


.selector-filter label{
    display:none;
}

.selector-available h2{
    margin:0px;
    
    line-height:20px;
    font-size: 14px;
}

.selector-available,
.selector-chosen {
    float: left;

    width:48%;
    margin-bottom: 5px;
    
    text-align: center;
}

.stacked .selector-available,
.stacked .selector-chosen,
.sorted-m2m-container{
    width:100%;
}

#content .selector-available h2,
#content .selector-chosen h2 {
    margin:0px;
    border: 1px solid $gray-lighter;

    line-height:20px;
    font-size: 14px;
}

#content .error .selector-available h2,
#content .error .selector-chosen h2 {
    border: 1px solid theme-color("danger");
    color: theme-color("danger");
}

.error .selector .selector-filter{
    border-color: theme-color("danger");
}

.selector .selector-available h2 {
    background-color: $white;
    // background-color: #FAFAFA;
    color: $gray;
}

.selector .selector-filter {
    margin: 0;
    border: 1px solid #ccc;
    border-width: 0 1px;
    padding: 3px;

    background: white;
    color: #999;
    font-size: 10px;
    text-align: left;
}

.selector .selector-filter label,
.inline-group .aligned .selector .selector-filter label {
    width: 16px;
    padding: 2px;
}

.selector .selector-available input[type="text"] {
    // height:32px;
    // width:100%;
    // padding-left: 28px;
	@extend .form-control;
    // background: url(../img/selector-search.gif) 8px 7px no-repeat;
    // @include box-sizing(border-box);
}


.selector ul.selector-chooser {
    float: left;

    width:4%;
    margin-top:60px;
    margin:0px;
    padding: 0;
}

.selector-chooser li {
    margin: 0;
    padding:10px 0px;

    text-align:center;
    list-style-type: none;
}

.selector select {
    margin-bottom: 10px;
    margin-top: 0;

    background: #FFF;
}
.selector .selector-chosen select{
    background: #f1f1f1;
}

.selector-add, .selector-remove {
    display:inline-block;
    width: 16px;
    height: 16px;
    overflow: hidden;
}

.selector-add {
    cursor: default;
}

.active.selector-add {
    cursor: pointer;
}

.selector-remove {
    cursor: default;
}

.active.selector-remove {
    cursor: pointer;
}


a.selector-chooseall {
    cursor: default;
}

a.active.selector-chooseall {
    cursor: pointer;
}

a.selector-clearall {
    cursor: default;
}

a.active.selector-clearall {
    cursor: pointer;
}

.stacked .selector-available {
    margin-bottom: 0;
}

.stacked .selector-available input {
    width: 442px;
}

.stacked ul.selector-chooser {
    clear:both;
    float:none;

    display:block;
    width:100%;
    height:32px;
    margin:0px;

    text-align: center;
}

.stacked .selector-chooser li {
    display:inline-block;
    padding: 4px;
}

.stacked .selector-chooseall, .stacked .selector-clearall {
    display: none;
}

.stacked .selector-add {
    cursor: default;
}

.stacked .active.selector-add {
    cursor: pointer;
}

.stacked .selector-remove {
    cursor: default;
}

.stacked .active.selector-remove {
    cursor: pointer;
}

.has-clock .at {
    line-height: 32px;
    margin: 0 15px;
    text-align: center;
    vertical-align: super;
}
.input-group-container.calendar,
.input-group-container.clock {
	display: inline-block;
    width: 157px;
}
.datetime {
	display: none;
}
.has-calendar.has-clock .control-label {
	display: block;
}

.admin-radio li {
    margin: 0px;
    padding: 0px;
    list-style: none;

    label {
        font-weight: normal;
        border: 1px solid $gray-lighter;
        background-color: lighten($gray-base, 95%);
        padding: 8px;   
    }
}

.panel .inline-deletelink {
    float: right;
    margin-top: -5px; 
}

