.table-inline .related-links {
    border: 1px solid  $gray-lighter;
    padding: 5px;
    height: 100%;
    background-color: lighten($gray-base, 95%);
}

.related-links a {
    opacity: 0.4;
    display: inline-block;
    margin: 0px 5px;
    &[href] {
        opacity: 1;
    }
}
th.delete,
td.delete {
    text-align: right;
}

.empty-form {
    display: none;
}

.timelist .btn {
    margin: 5px 3px;
}
.clockbox .calendar-cancel {
    text-align: center;
}
.clockbox {
    top: auto;
    bottom: 105%;
    left: -30px;

    min-width: 332px;
}

.calendarbox .calendar-shortcuts {
    position: relative;
    top: -15px;
        
    text-align: center;
}
.input-group-container {
    position: relative;
}

.calendarbox caption{
    position: absolute;
    top: 0;
    left: 35%;
}
.calendarbox {
    top: auto;
    bottom: 105%;
    left: -24px;

    width: 320px;
    td {
        text-align: center;
    }
}