body.login {
    .login-container{
        margin: auto;
        display: inline-block;
        max-width: 600px;
    }
    h1 {
        color: theme-color("primary");
        font-size: 1.6em;
    }
    form {
        background-color: $card-cap-bg;
        border: 1px $card-border-color solid;
        padding: 15px;
        margin-top: 28px;
        box-shadow: 0 10px 68px 0 rgba(0,0,0,0.1);
    }
}

.login-layout {
  padding-top: 80px;
}